<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <Row>
      <Col span="12">
        <Form-item prop="name" label="">
          <Input v-model="formValidate.name" placeholder=""></Input>
        </Form-item>
      </Col>
      <Col span="12">
        <Form-item prop="shortcut" label="">
          <Input v-model="formValidate.shortcut" placeholder=""></Input>
        </Form-item>
      </Col>
    </Row>
    <Row>
      <Col span="12">
        <Form-item prop="path" label="">
          <Input v-model="formValidate.path" placeholder=""></Input>
        </Form-item>
      </Col>
      <Col span="12">
        <Form-item prop="port" label="">
          <Input-number :max="65536" :min="1" v-model="formValidate.port" placeholder=""></Input-number>
        </Form-item>
      </Col>
    </Row>
    <Row>
      <Col span="12">
        <Form-item label="" prop="parentId">
          <Select v-model="formValidate.parentId" placeholder="">
            <Option value=""></Option>
            <Option v-for="item in projectList" :value="item.id" :key="item.id">{{item.name}}</Option>
          </Select>
        </Form-item>
      </Col>
      <Col span="12">
        <Form-item prop="proxyTo" label="404">
          <Input v-model="formValidate.proxyTo" placeholder=""></Input>
        </Form-item>
      </Col>
    </Row>
    <Form-item prop="injectHtml" label="html">
      <i-switch v-model="formValidate.injectHtml">
        <span slot="open"></span>
        <span slot="close"></span>
      </i-switch>
    </Form-item>
    <Row>
      <Col span="12">
        <Form-item class="cus-form-json-editor" label="">
          <jsonEditor v-model="formValidate.staticPath"></jsonEditor>
        </Form-item>
      </Col>
      <Col span="12">
        <Form-item class="cus-form-json-editor" label="">
          <jsonEditor v-model="formValidate.proxyTable"></jsonEditor>
        </Form-item>
      </Col>
    </Row>
    <Row>
      <Col span="12">
        <Form-item class="cus-form-json-editor" label="URL">
          <jsonEditor v-model="formValidate.urls"></jsonEditor>
        </Form-item>
      </Col>
      <Col span="12">
        <Form-item class="cus-form-json-editor" label="GULP">
          <jsonEditor v-model="formValidate.gulp"></jsonEditor>
        </Form-item>
      </Col>
    </Row>

    <Form-item>
      <Button type="primary" @click="handleSubmit('formValidate')"></Button>
    </Form-item>
  </Form>
</template>
<script>
  import jsonEditor from '../code-editor/json-editor.vue'
  import { addProject, editProject } from '../../api/api.js'

  export default {
    data () {
      return {
        formValidate: {
          name: '',
          shortcut: '',
          path: '',
          port: 8080,
          parentId: '',
          proxyTo: '',
          injectHtml: true,
          proxyTable: [],
          staticPath: [],
          urls: [],
          gulp: {},
        },
        ruleValidate: {
          name: [{
            trigger: 'blur',
            message: '',
            required: true,
          }],
          shortcut: [{
            message: '',
            required: true,
          }],
          port: [{
            type: 'number',
            required: true,
          }],
        }
      }
    },
    computed: {
      projectList () {
        return this.$store.getters['project/selector']
      },
    },
    components: {
      jsonEditor
    },
    mounted () {
      this.checkProjectList().then(() => {
        if (this.$route.query.id) {
          let id = this.$route.query.id
          let proj = this.$store.state.project.list.find((p) => { return p._id === id })
          if (proj) {
            this.copyToObj(this.formValidate, proj)
            this.formValidate.id = proj._id
          } else {
            this.$Message.error('')
          }
        }
      })
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.postProject(this.formValidate).then((data) => {
              if (!data.code) {
                this.$Message.success(data.data.tip)
                this.freshProject()
              }
            })
          } else {
            this.$Message.error('!')
          }
        })
      },
      postProject () {
        let func = this.formValidate.id ? editProject : addProject
        return func.apply(this, arguments)
      },
    }
  }
</script>

<style scoped>
.cus-form-json-editor {
  width: 100%;
}
.cus-form-json-editor>span {
  flex-basis: 80px;
}
</style>



